import { fileUpload } from '../elements';

if (fileUpload.self !== null) {
    const scriptSrc = document.createElement('script');
    scriptSrc.src = 'https://cdnjs.cloudflare.com/ajax/libs/jquery/3.4.1/jquery.min.js';
    scriptSrc.async = 'true';
    document.head.appendChild(scriptSrc);

    const script = document.createElement('script');
    script.innerHTML = `
    document.addEventListener('DOMContentLoaded', function() {
        document.getElementById('input_file').addEventListener('change', function() {
            selectFile(this.files);
            //this.files[0].size gets the size of your file.
            //alert(this.files[0].size);
        });
    });

    //   
    var fileIndex = 0;
    //    
    var totalFileSize = 0;
    //  
    var fileList = new Array();
    //   
    var fileSizeList = new Array();
    //     MB
    var uploadSize = 50;
    //      MB
    var maxUploadSize = 500;

    // $(function() {
    //   
    fileDropDown();
    // });

    //   
    function fileDropDown() {
        var dropZone = document.getElementById('dropZone');
        //Drag
        dropZone.addEventListener('dragenter', function(e) {
            e.stopPropagation();
            e.preventDefault();
            //   css
            dropZone.style.backgroundColor = '#E3F2FC';
        });
        dropZone.addEventListener('dragleave', function(e) {
            e.stopPropagation();
            e.preventDefault();
            //   css
            dropZone.style.backgroundColor = '#FFFFFF';
        });
        dropZone.addEventListener('dragover', function(e) {
            e.stopPropagation();
            e.preventDefault();
            //   css
            dropZone.style.backgroundColor = '#E3F2FC';
        });
        dropZone.addEventListener('drop', function(e) {
            e.preventDefault();
            //   css
            dropZone.style.backgroundColor = '#FFFFFF';

            var files = e.dataTransfer.files;
            if (files != null) {
                if (files.length < 1) {
                    /* alert("  "); */
                    console.log('  ');
                    return;
                } else {
                    selectFile(files);
                }
            } else {
                alert('ERROR');
            }
        });
    }

    //  
    function selectFile(fileObject) {
        var files = null;

        if (fileObject != null) {
            //  Drag  
            files = fileObject;
        } else {
            //   
            // files = $('#multipaartFileList_' + fileIndex)[0].files;
        }

        //  
        if (files != null) {
            if (files != null && files.length > 0) {
                document.getElementById('fileDragDesc').style.display = 'none';
                document.getElementById('fileListTable').style.display = 'block';
            } else {
                document.getElementById('fileDragDesc').style.display = 'block';
                document.getElementById('fileListTable').style.display = 'none';
            }

            for (var i = 0; i < files.length; i++) {
                //  
                var fileName = files[i].name;
                var fileNameArr = fileName.split('.');
                // 
                var ext = fileNameArr[fileNameArr.length - 1];
                console.log(ext);

                var fileSize = files[i].size; //  ( :byte)
                console.log('fileSize=' + fileSize);
                if (fileSize <= 0) {
                    console.log('0kb file return');
                    return;
                }

                var fileSizeKb = fileSize / 1024; //  ( :kb)
                var fileSizeMb = fileSizeKb / 1024; //  ( :Mb)

                var fileSizeStr = '';
                if (1024 * 1024 <= fileSize) {
                    //   1  
                    console.log('fileSizeMb=' + fileSizeMb.toFixed(2));
                    fileSizeStr = fileSizeMb.toFixed(2) + ' Mb';
                } else if (1024 <= fileSize) {
                    console.log('fileSizeKb=' + parseInt(fileSizeKb));
                    fileSizeStr = parseInt(fileSizeKb) + ' kb';
                } else {
                    console.log('fileSize=' + parseInt(fileSize));
                    fileSizeStr = parseInt(fileSize) + ' byte';
                }

                var extensions = [
                    'hwp',
                    'doc',
                    'docx',
                    'xls',
                    'xlsx',
                    'ppt',
                    'pptx',
                    'txt',
                    'png',
                    'pdf',
                    'jpg',
                    'jpeg',
                    'gif',
                    'zip'
                ];
                if (extensions.indexOf(ext) == -1) {
                    //  
                    /* alert("   .");
                        break; */
                    alert('   .(' + fileName + ')');
                } else if (fileSizeMb > uploadSize) {
                    //   
                    alert(' !    : ' + uploadSize + ' MB');
                    break;
                } else {
                    //   
                    totalFileSize += fileSizeMb;

                    //   
                    fileList[fileIndex] = files[i];

                    //    
                    fileSizeList[fileIndex] = fileSizeMb;

                    //    
                    addFileList(fileIndex, fileName, fileSizeStr);

                    //   
                    fileIndex++;
                }
            }
        } else {
            alert('ERROR');
        }
    }

    //    
    function addFileList(fIndex, fileName, fileSizeStr) {
        /* if (fileSize.match("^0")) {
                alert("start 0");
            } */

        var trEle = document.createElement('tr');
        trEle.id = 'fileTr_' + fIndex;

        var tdEle = document.createElement('td');
        tdEle.innerText = fileName + ' (' + fileSizeStr + ')';

        var deleteBtn = document.createElement('input');
        deleteBtn.type = 'button';
        deleteBtn.value = '';
        deleteBtn.setAttribute('onclick', 'deleteFile(' + fIndex + '); return false;');

        trEle.appendChild(tdEle).appendChild(deleteBtn);
        document.getElementById('fileTableTbody').appendChild(trEle);
    }

    //   
    function deleteFile(fIndex) {
        console.log('deleteFile.fIndex=' + fIndex);
        //    
        totalFileSize -= fileSizeList[fIndex];

        //   
        delete fileList[fIndex];

        //    
        delete fileSizeList[fIndex];

        //     
        // $('#fileTr_' + fIndex).remove();
        var targetEle = document.getElementById('fileTr_' + fIndex);
        targetEle.parentNode.removeChild(targetEle);

        console.log('totalFileSize=' + totalFileSize);

        if (totalFileSize > 0) {
            document.getElementById('fileDragDesc').style.display = 'none';
            document.getElementById('fileListTable').style.display = 'block';
        } else {
            document.getElementById('fileDragDesc').style.display = 'block';
            document.getElementById('fileListTable').style.display = 'none';
        }
    }

    //  
    function uploadFile() {
        //   
        var uploadFileList = Object.keys(fileList);

        //   
        if (uploadFileList.length == 0) {
            //  
            alert(' .');
            return;
        }

        //  500MB    
        if (totalFileSize > maxUploadSize) {
            //    
            alert('  !     : ' + maxUploadSize + ' MB');
            return;
        }

        if (confirm(' ?')) {
            //    formData  
            var form = $('#uploadForm');
            var formData = new FormData(form);
            for (var i = 0; i < uploadFileList.length; i++) {
                formData.append('files', fileList[uploadFileList[i]]);
            }

            $.ajax({
                url: ' ',
                data: formData,
                type: 'POST',
                enctype: 'multipart/form-data',
                processData: false,
                contentType: false,
                dataType: 'json',
                cache: false,
                success: function(result) {
                    if (result.data.length > 0) {
                        alert('');
                        location.reload();
                    } else {
                        alert('');
                        location.reload();
                    }
                }
            });
        }
    }
    `;
    document.body.appendChild(script);
}
